import { useEffect, useState } from "react";
import { useWebsocket } from "./useWebsocket";
import { parser, stringify } from "../helpers";
import { EventsData } from "../types";

const useEvents = () => {
    const [events, setEvents] = useState<EventsData>([])
    const { websocket } = useWebsocket()
    
    useEffect(() => {
      if (!websocket) {
        return;
      }
      websocket.onmessage = (e) => {
        const { data, type } = parser(e.data);
    
        switch (type) {
          case "LIVE_EVENTS_DATA":
            setEvents(data)
            break;
          case "MARKET_DATA":
            console.log(data)
            break;
          default:
              return;
        }
    
      };
    }, [websocket, events]);

    // const getMarkets = (marketIds: number[]) => {
    //   if (!websocket) return
    //   marketIds.forEach(marketId => websocket.send(JSON.stringify({ type: "getMarket", id: marketId })))
    // }

    return {
        events,
        // getMarkets
    }
}

export {
    useEvents
}

